.about-container {
  margin-bottom: -10px;
}

.textp {
  margin-top: -10em;
  font-size: 2em;
}

.paragraphp {
  margin-top: 10em;
  font-size: 1.4em;
  padding: 250px 50px;

}

.skills-container {
  display: flex;
  justify-content: center;
  padding: 10px;
  margin-top: -32em;
}

.skills-container-about {
  display: inline-block;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

.textpp {
  font-weight: 800;
}

.skills {
  display: inline-block;

  background-color: #f9cf71;
  margin: 16px;
  padding: 8px 20px;
  border-radius: 50px;
  text-align: center;
  font-weight: bold;
}

.skills-container-about p.textpp {
  margin-bottom: 10px;
}

.text-and-image-container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;


    @media screen and (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }

}

.aboutimg {
  max-width: 20%;
  margin-left: 2em;
  border-radius: 10px;
  margin-top: 14em;
  animation: floatAnimation 3s ease infinite alternate;
   @media screen and (max-width: 768px) {
    max-width: 50%;
    margin: 0;
    margin-top: 2em;
  }

}

@keyframes floatAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}

.icon {
  font-size: 4em;
  margin: 15px;
  color: #867ba3;
  animation: spin 4s linear infinite;
    @media screen and (max-width: 768px) {
    font-size: 3em;
  }

}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 460px) {
.textp{
  font-size: 1.5em;
  margin-top: -10em;
}
.texts-container{
  font-size: 1.3em;
}
p.paragraphp{
  font-size: 1em;
  margin-top: 1em;

}
}
.skills-container {
  margin-top: -10em;
}

.footerabout {
  margin-top: 12em;
    @media screen and (max-width: 768px) {
    margin-top: 20px;
    }
}
