.cursor {
  position: fixed;
  width: 40px; 
  height: 40px; 
  font-size: 40px; 
  border: 4px solid orangered;
  margin-left: -20px;
  margin-top: -20px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 100;
}


.grow {
  transform: scale(1.2);
}


.grow-small {
  transform: scale(0.8);
}
