@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');


* {
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
html, body {
     font-family: "Roboto Mono", monospace;
  font-weight: 400;
  font-style: normal;
    background: #ffffff;
}



.slide-in {
    position: fixed;
    z-index: 1;
    top:0;
    left:0;
    width: 100%;
    height: 100vh;
    background-color: #f9cf71;
    transform-origin:bottom ;

}

.slide-out {
    position: fixed;
    top:0;
    left:0;
    z-index: 1;
    width: 100%;
    height: 100vh;
    background-color: #867ba3;
    transform-origin:top ;
}
