.roundedButton {
border-radius: 3em;
border: 1px solid rgb(136, 136, 136);
position: relative;
display: flex;
align-items: center;
justify-content: center;
padding: 15px 60px 15px;
  cursor: none;

}
p {
    position: relative;
    z-index: 1;
    transition: color 0.4s linear;
}

p:hover {
    p{
        color: black;
    }
}


.circle {
    width: 100%;
    height: 150%;
    position: absolute;
    border-radius: 50%;
    top:200%;
}
